import React from "react";
import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from "@mui/material";

export const Informoj = ({ dialogOpen, setDialogOpen }) => {
  return (
    <Dialog open={dialogOpen}>
      <DialogTitle>Facililo</DialogTitle>
      <DialogContent>
        <h2>Kio estas <em>Facililo</em>?</h2>

        <p>Per <em>Facililo</em> eblas kontroli, u la enskribita teksto
          enhavas la radikojn en
          la <a href="https://uea.facila.org/paroli/">vortolisto</a> uzata
          de <a href="https://uea.facila.org">uea.facila.org</a>. Se radiko
          trovias en tiu vortlisto, i estos signita verde a blue.</p>

        <noscript><p>i funkcias plene per Javascript, sed via retumilo ne
          subtenas Javascript, a i estas malaltita!  altu ruligadon de
          Javascript, a uzu alian retumilon, por povi uzi la
          facililon.</p></noscript>

        <ul>
          <li>Verda signo: la vorto aperas en la listo Tre facila. </li>
          <li>Blua signo: la vorto aperas en la listo Facila.</li>
          <li>Rua signo: la radiko tute ne aperas en la vortlisto. </li>
        </ul>

        <h2>Se aperos la rua signo</h2>

        <p>Kiam oni verkas tekston por la retejo uea.facila, se vorto estas
          signita rue, necesas ani in a aldoni difinon. </p>

        <p>Notu tamen, ke krom la radikoj en la listo, oni anka rajtas uzi la
          nomojn de homoj, landoj, lingvoj ks. <em>Facililo</em> signas ilin per
          rua signo, ar ili ne trovias en la listo. Ili tamen estas
          akcepteblaj, kaj ne necesas aldoni por ili apartan difinon.</p>

        <h2><em>Facililo</em> foje miskomprenas</h2>

        <p>Kelkaj kunmetaoj erarigas <em>Facililo</em>n. Ekzemple la radiko
          tropik- tute ne trovias en la listo, sed <em>Facililo</em> signas
          la vorton tropika blue, ar i aspektas kiel kunmetao de du
          permesataj eroj: tro kaj pik-.</p>

        <h2><em>Facililo</em> ne kontrolas facilecon de la lingvao</h2>

        <p><em>Facililo</em> nur kontrolas, u eblas konstrui la enskribitajn
          vortojn per la permesataj radikoj. i foje akceptas sensencajn
          vortojn, kaj tute ne kontrolas la gramatikon.</p>

        <p>Se la radikoj trovias en la listo, <em>Facililo</em> signas
          malfacilajn kunmetaojn, ekz. interligiteco, kiel facilajn. La
          verkanto devas mem decidi, u lernanto senprobleme komprenus tian
          vorton. Facila teksto prefere enhavu simplajn frazojn kaj ne uzu
          pezajn kunmetaojn. Tial estas preferinde anstataigi in per la
          esprimo la ligoj inter...</p>

        <h2>La fontokodo estas libera</h2>

        <p><a href="http://github.com/legoscia/facililo">La fontokodo de la
          facililo</a> estas libere havebla la la permesilo de MIT.  Tio
          signifas ke oni rajtas iel ajn uzi, kopii kaj ani in, kondie
          nur ke oni agnoskas mian atorecon.</p>

        <p>i estas konstruita surbaze de la
          biblioteko <a href="http://knockoutjs.com/">Knockout</a>, kiun vi
          nepre provu se vi programas en Javascript.</p>

        <h2>i ne estas mia originala ideo</h2>

        <p>Mi ekhavis inspiron de <a href="http://splasho.com/upgoer5/">The
          Up-Goer Five Editor</a>, kiu simile limigas la uzanton al la mil
          plej oftaj vortoj en la angla lingvo.  i siavice estas inspirita
          la <a href="http://xkcd.com/1133/">komikso "Up Goer Five" de
            xkcd</a>, kiu priskribas la partojn de kosmoipo kun la sama
          limigo.</p>

        <h2>u vi havas demandon, komenton, a proponon?</h2>

        <p>Se vi estas membro de Facebook, vi povas afii komentojn
          e <a href="https://www.facebook.com/Facililo">la pao de
            la Facililo</a>.
        </p>

        <p>Se vi trovis eraron, kaj atus prilabori la ilon por solvi la
          problemon, <a href="https://github.com/legoscia/facililo/issues">kreu
            problemraporton e GitHub</a>.
        </p>

        <p>A vi povas sendi retmesaon
          al <a href="mailto:magnus.henoch@gmail.com">magnus.henoch@gmail.com</a>.
        </p>
      </DialogContent>
      <DialogActions>
        <Button onClick={() => setDialogOpen(false)} color="primary">
          Fermu
        </Button>
      </DialogActions>
    </Dialog>
  );
}